import React from "react";
import "../../component-styles/Arkusze.scss";
import { Link, useNavigate } from "react-router-dom";
import { Link as ScrollLink } from "react-scroll";
import { useAtomValue, useSetAtom } from "jotai";
import { MotywStrony } from "../../App.tsx";
import { ZakresArkusze } from "./Arkusze.tsx";
function ArkuszeStart() {
  const setZakresArkusze = useSetAtom(ZakresArkusze);

  const motyw = useAtomValue(MotywStrony);
  const navigate = useNavigate();

  return (
    <div className="arkusze-segment">
      <div className="welcome-arkusze">
        <div className="img-and-start">
          <img
            src={
              motyw === "light"
                ? process.env.PUBLIC_URL + "/images/ArkuszeWelcome.png"
                : process.env.PUBLIC_URL + "/images/ArkuszeWelcomeDarkMode.png"
            }
            alt="arkusze logo"
          />

          <div className="welcome-arkusze-start">
            Jak dobrze zda <span className="purple-text">matur</span>?
          </div>
        </div>
        <p className="welcome-arkusze-content">
          Odpowied to <strong>zadania!</strong>
          <br />
          Mimo, e istnieje wiele porad i sposobw jak przygotowa si do
          matury, czyli tak zwanego egzaminu dojrzaoci, to z pewnoci jednym
          z podstawowych krokw jakie powinien podj kady maturzysta jest
          przerobienie arkuszy z poprzednich lat.
          <br />W zwizku z tym zebralimy dla was
          <strong> matury z ubiegych lat</strong> oraz przygotowalimy ich{" "}
          <strong>rozwizania wraz z objanieniami!</strong>
        </p>
        <ScrollLink to="wybierz-poziom" spy={true} smooth={true} duration={600}>
          <div className="arrows-div">
            <div className="arrows">
              <i className="fa-solid fa-chevron-down first" />
              <i className="fa-solid fa-chevron-down second" />
              <i className="fa-solid fa-chevron-down third" />
              <i className="fa-solid fa-chevron-down fourth" />
            </div>
          </div>
        </ScrollLink>
      </div>
      <div id="wybierz-poziom" className="wybierz-poziom">
        <div className="wybierz-swoj-poziom wybierz">Wybierz</div>
        <div className="strzalka1">
          <img
            src={
              motyw === "light"
                ? process.env.PUBLIC_URL + "/images/Strzalka.png"
                : process.env.PUBLIC_URL + "/images/StrzalkaDarkMode.png"
            }
            alt=""
          />
        </div>
        <div className="wybierz-poziom-buttons">
          <button
            onClick={() => {
              navigate("/arkusze/lista");
              setZakresArkusze("podstawa");
            }}
          >
            <div className="matematyka">Matematyka</div>
            <div className="poziom">Poziom podstawowy</div>
          </button>
          <button
            onClick={() => {
              navigate("/arkusze/lista");
              setZakresArkusze("rozszerzenie");
            }}
          >
            <div className="matematyka">Matematyka</div>
            <div className="poziom">Poziom rozszerzony</div>
          </button>
        </div>
        <div className="strzalka2">
          <img
            src={
              motyw === "light"
                ? process.env.PUBLIC_URL + "/images/Strzalka.png"
                : process.env.PUBLIC_URL + "/images/StrzalkaDarkMode.png"
            }
            alt=""
          />
        </div>
        <div className="wybierz-swoj-poziom swoj-poziom">swj poziom</div>
      </div>
      <div className="wzorowanie-ad">
        <img
          src={process.env.PUBLIC_URL + "/images/WzorowanieReklama.png"}
          alt="wzorowanie link"
          onClick={() => {
            window.open("https://www.wzorowanie.pl/", "_blank");
          }}
        />
        <div className="wzorowanie-ad-content">
          Chcesz przygotowa si do{" "}
          <span className="purple">matury z matematyki</span>? Oto{" "}
          <span className="purple">kurs maturalny</span>, ktry ci w tym pomoe!
          A jeli chcesz przygotowa si na najbliszy{" "}
          <span className="purple">sprawdzian</span>, signij po{" "}
          <span className="purple">bezpatne</span> materiay z kanau{" "}
          <span className="green">Wzorowanie</span>, ktre znajdziesz{" "}
          <Link
            to="https://www.youtube.com/@wzorowanie/videos"
            target="_blank"
            className="link"
          >
            tutaj
          </Link>
        </div>
      </div>
    </div>
  );
}

export default ArkuszeStart;
