import React from "react";
import "../component-styles/Kontakt.scss";
import ClipboardJS from "clipboard";

function Kontakt() {
  const clipboard = new ClipboardJS(".copy");
  clipboard.on("success", (e) => {
    e.clearSelection();
  });
  return (
    <div id="kontakt-segment">
      <header className="welcome-kontakt">
        <span className="line">
          Skontaktuj si z <span className="purple-text">nami</span>!
        </span>
      </header>
      <main className="kontakt-info">
        <div className="matzone-kontakt kontakt-div">
          <h1>E-mail matzone:</h1>
          <div className="info">
            Mail:{" "}
            <span
              className="copy"
              data-clipboard-text="matzone.kontakt@gmail.com"
            >
              matzone.kontakt@gmail.com
            </span>
          </div>
        </div>
        <div className="korepetycje-template kontakt-div">
          <h1>Zgoszenia korepetycji:</h1>
          <div className="info">
            <span className="zgloszenia-start">
              <p>Jeste korepetytorem i chcesz pojawi si na stronie? Napisz do
              nas wiadomo, a za darmo umiecimy ci w zakadce "Korepetycje"!</p>
              We wiadomoci powinny znajdowa si twoje dane, takie jak:
            </span>
            <p className="zgloszenia-info">
              <ul>
                <li>
                  <b>Imi i nazwisko</b>,
                </li>
                <li>
                  <b>Miejscowo</b>,
                </li>
                <li>
                  <b>Numer telefonu</b>,
                </li>
                <li>
                  <b>E-mail</b>,
                </li>
                <li>
                  <b>Zakres, ktrego uczysz</b>,
                </li>
                <li>
                  <b>Forma lekcji</b>,
                </li>
              </ul>
              Wiadomo urozmaici moesz rwnie piszc troch o sobie.
            </p>
          </div>
        </div>
      </main>
    </div>
  );
}

export default Kontakt;
