import React from "react";
import "../component-styles/HomePage.scss";
import { useAtom } from "jotai";
import { NavLink } from "react-router-dom";
import { Link as ScrollLink} from 'react-scroll';
import { MotywStrony } from "../App.tsx";
function HomePage() {
  const [motyw] = useAtom(MotywStrony);

  return (
    <div id="home-page">
      <header className="home-header">
        <div className="welcome-home-page">
          Caa wiedza w <br />
          <span className="purple-text">jednym miejscu!</span>
        </div>
        <div className="big-buttons">
          <NavLink
            className="big-buttons-button"
            to="/materialy"
            data-content="Materiay"
          >
            <img
              src={
                motyw === "light"
                  ? process.env.PUBLIC_URL + "/images/HomePageLinks/Materialy.png"
                  : process.env.PUBLIC_URL + "/images/HomePageLinks/MaterialyDarkMode.png"
              }
              alt="materialy"
            />
          </NavLink>

          <NavLink
            className="big-buttons-button"
            to="/korepetycje"
            data-content="Korepetycje"
          >
            <img
              src={
                motyw === "light"
                  ? process.env.PUBLIC_URL + "/images/HomePageLinks/Korepetycje.png"
                  : process.env.PUBLIC_URL + "/images/HomePageLinks/KorepetycjeDarkMode.png"
              }
              alt="korepetycje"
            />
          </NavLink>

          <NavLink
            className="big-buttons-button"
            to="/studia"
            data-content="Studia"
          >
            <img
              src={
                motyw === "light"
                  ? process.env.PUBLIC_URL + "/images/HomePageLinks/Studia.png"
                  : process.env.PUBLIC_URL + "/images/HomePageLinks/StudiaDarkMode.png"
              }
              alt="studia"
            />
          </NavLink>

          <NavLink
            className="big-buttons-button"
            to="/arkusze"
            data-content="Arkusze"
          >
            <img
              src={
                motyw === "light"
                  ? process.env.PUBLIC_URL + "/images/HomePageLinks/Arkusze.png"
                  : process.env.PUBLIC_URL + "/images/HomePageLinks/ArkuszeDarkMode.png"
              }
              alt="arkusze"
            />
          </NavLink>
        </div>
        <ScrollLink
          to="o-stronie-div"
          spy={true} smooth={true} duration={600}
        >
          <div className="arrows-div">
            <div className="arrows">
              <i className="fa-solid fa-chevron-down first" />
              <i className="fa-solid fa-chevron-down second" />
              <i className="fa-solid fa-chevron-down third" />
              <i className="fa-solid fa-chevron-down fourth" />
            </div>
          </div>
        </ScrollLink>
      </header>
      <div id="o-stronie-div" className="o-stronie-div">
        <header className="o-stronie-welcome">
          <span className="line">
            Co wyrnia{" "}
            <span className={`mat ${motyw === "light" ? "purple-text" : ""}`}>
              Mat
            </span>
            <span className={`zone ${motyw === "light" ? "" : "purple-text"}`}>
              Zone
            </span>
            ?
          </span>
        </header>

        <nav className="o-stronie-nav">
          <div
            className="o-stronie-nav-item materialy-video"
            data-content="Na naszej stronie znajdziesz wiele wartociowych filmikw dopasowanych do konkretnych zagadnie, ktre z pewnoci pomog Ci opanowa materia szkolny. "
          >
            <img
              src={process.env.PUBLIC_URL + "/images/MaterialyVideo.png"}
              alt=""
            />
            <div>Materiay video</div>
          </div>
          <div
            className="o-stronie-nav-item rozwiazania-matur"
            data-content="Chcesz jak najlepiej przygotowa si do matury? Przerb arkusze z poprzednich lat. Na naszej stronie masz je wszystkie w jednym miejscu wraz z odpowiedziami i wyjanieniami zada."
          >
            <img
              src={process.env.PUBLIC_URL + "/images/RozwiazaniaMatur.png"}
              alt=""
            />
            <div>Rozwizania matur</div>
          </div>
          <div
            className="o-stronie-nav-item teoria-i-artykuly"
            data-content="Oprcz materiaw video znajdziesz u nas te ca potrzebn teori i wzory do kadego tematu (nawet te ktrych nie ma w karcie wzorw), co wicej moesz przeczyta wiele ciekawych i wartociowych artykuw edukacyjnych i nowinkowych takich jak na przykad najnowsze zmiany w wymaganiach maturalnych."
          >
            <img
              src={process.env.PUBLIC_URL + "/images/TeoriaIArtykuly.png"}
              alt=""
            />
            <div>Teoria i artykuy</div>
          </div>
        </nav>
        <section className="dodatki">
          <hr />
          <div className="dodatki-div zmiany-maturalne-div">
            <div className="zmiany-maturalne-start">
              Wszystkie informacje o zmianach maturalnych w{" "}
              <span className="purple-text">jednym miejscu</span>
            </div>
            <p className="zmiany-maturalne-content">
              W tej zakadce znajdziesz wszystkie potrzebne ci informacje
              dotyczce tego jakie zmiany pojawi si na nadchodzcych maturach
              oraz fakty ze wiata dotyczce edukacji. Wejd i bd na bieco!
            </p>
            <NavLink to="zmiany-maturalne" className="zmiany-maturalne-link">
              Kliknij po wszystkie informacje
            </NavLink>
            <img
              src={process.env.PUBLIC_URL + "/images/ZmianyMaturalne.png"}
              alt="zmiany maturalne"
            />
          </div>
          <hr />
          <div className="dodatki-div artykuly">
            <div className="artykuly-start">Artykuy</div>
            <div className="artykuly-divs">
              <NavLink
                to="artykul/metody-efektywnego-uczenia-sie"
                className="artykuly-link metody-efektywnego-uczenia-sie-div"
              >
                <img
                  src={process.env.PUBLIC_URL + "/images/Arkusze/MetodyEfektywnegoUczeniaSie.png"}
                  alt="metody efektywnego uczenia si"
                />
                <span>Metody efektywnego uczenia si</span>
              </NavLink>
              <NavLink
                to="artykul/przygotowanie-do-matury"
                className="artykuly-link przygotowanie-do-matury-div"
              >
                <img
                  src={process.env.PUBLIC_URL + "/images/Arkusze/PrzygotowanieDoMatury.png"}
                  alt="przygotowanie do matury"
                />
                <span>Przygotowanie do matury</span>
              </NavLink>
              <NavLink
                to="artykul/motywacja-do-systematycznej-nauki"
                className="artykuly-link przygotowanie-do-matury-div"
              >
                <img
                  src={process.env.PUBLIC_URL + "/images/Arkusze/MotywacjaDoSystematycznejNauki.png"}
                  alt="motywacja do systematycznej nauki"
                />
                <span>Motywacja do systematycznej nauki</span>
              </NavLink>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
}
export default HomePage;
