import React from "react";
import "../component-styles/Studia.scss";
import { useAtom } from "jotai";
import { MotywStrony } from "../App.tsx";
function Studia() {
  const [motyw] = useAtom(MotywStrony);
  return (
    <div id="studia">
      <div className="studia-segment">
        <div className="welcome-studia">
          <div className="welcome-studia-start">
            Studia po matematyce rozszerzonej
          </div>
          <div className="welcome-studia-img-and-content">
            <div className="welcome-studia-img">
              <img
                src={
                  motyw === "light"
                    ? process.env.PUBLIC_URL + "/images/StudiaWelcome.png"
                    : process.env.PUBLIC_URL +
                      "/images/StudiaWelcomeDarkMode.png"
                }
                alt="Szkola Glowna Handlowa W Warszawie"
              />
            </div>
            <div className="welcome-studia-content">
              Obecnie zdajc matematyk rozszerzon masz szeroki wybr kierunkw
              studiw i uczelni w caej Polsce. Nauki, na ktre moesz si
              zdecydowa, to midzy innymi:
              <ul>
                {[
                  "Matematyka",
                  "Ekonomia",
                  "Informatyka",
                  "Finanse",
                  "Medycyna",
                  "Administracja",
                  "Energetyka",
                  "Architektura",
                  "Geologia",
                ].map((x, index) => (
                  <li className={x + "-list"} key={x + index}>
                    {x}
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
        <div
          className="uczelnia-segment szkola-glowna-handlowa"
          onClick={() => {
            window.open("https://www.sgh.waw.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text szkola-glowna-handlowa-text">
            <div className="uczelnia-segment-start szkola-glowna-handlowa-start">
              <span>Szkoa gwna handlowa w Warszawie</span>
            </div>
            <div className="uczelnia-segment-content szkola-glowna-handlowa-content">
              Rekrutacja na SGH nie jest prosta, oprcz zdania matur uczestnicy
              musz podejs do egzaminu wstpnego pod nazw "test z wiedzy o
              przedsibiorczosci", ktry liczy si tak samo jak matura z
              matematyki!
              <br /> Prg punktowy na rok 2023- 306 pkt
              <br /> SGH w Warszawie to najstarsza i najbardziej prestiowa
              uczelnia ekonomiczna w polsce, a take jeden z najlepszych
              uniwersytetw o tym profilu w europie, dlatego warto wzi go pod
              uwage.
              <br /> Mylc o tej uczelni celuj w bardzo wysokie wyniki na
              maturze.
            </div>
          </div>
          <div className="uczelnia-segment-img szkola-glowna-handlowa-img">
            <img
              src={
                process.env.PUBLIC_URL +
                "/images/Uczelnie/SzkolaGlownaHandlowaWWarszawie.png"
              }
              alt="Szkola Glowna Handlowa W Warszawie"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment akademia-leona-kozminskiego"
          onClick={() => {
            window.open("https://www.kozminski.edu.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text akademia-leona-kozminskiego-text">
            <div className="uczelnia-segment-start akademia-leona-kozminskiego-start">
              <span>Akademia Leona Komiskiego w Warszawie</span>
            </div>
            <div className="uczelnia-segment-content akademia-leona-kozminskiego-content">
              Akademia Leona Komiskiego uznawana jest za najlepsz szko
              biznesu w Europi centralnej. Specjalizuje si gwnie w
              dziedzinie nauk spoecznych, oferujc zrnicowany wybr programw
              z zakresu ekonomii, zarzdzania, prawa, administracji oraz nauk
              spoecznych. Uczelnia ma reputacj zaangaowania w badania
              naukowe, a take siln wspprac z midzynarodowymi instytucjami.
              <br /> Na studia w jzyku polskim zostaj przyjci kandydaci,
              ktrzy osigneli conajmniej 300 punktw w rekrutacji (1% na
              maturze podstawowej - 1,5pkt 1% na maturze rozszerzonej - 2pkt 1%
              na maturze rozszerzonej z matematyki 3pkt!)
              <br /> Dla finalistw i laureatw olimpiady "Zwolnieni z Teorii"
              przypada rwnie dodatkowe 30pkt.
            </div>
          </div>
          <div className="uczelnia-segment-img akademia-leona-kozminskiego-img">
            <img
              src={
                process.env.PUBLIC_URL + "/images/Uczelnie/AkademiaLeonaKozminskiego.png"
              }
              alt="Akademia Leona Komiskiego"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment akademia-gorniczo-hutnicza"
          onClick={() => {
            window.open("https://www.agh.edu.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text akademia-gorniczo-hutnicza-text">
            <div className="uczelnia-segment-start akademia-gorniczo-hutnicza-start">
              <span>Akademia Grniczo Hutnicza w Krakowie</span>
            </div>
            <div className="uczelnia-segment-content akademia-gorniczo-hutnicza-content">
              AGH jest jedn z topowych polskich publicznych uczelni
              technicznych. Prowadzi si tam nauczanie w kierunkach cisych,
              technicznych, biznesowych i wiele wicej. Uczelnia ma bogat
              histori zaczynajc si od 1919 roku. Samo otoczenie agh jest
              dodatkowo zachcajce dla chccych tam studiowa. Maksymalna
              warto wskanika rekrutacji tej uczelni wynosi 1000 pkt.
              Kandydaci na pierwszy rok studiw stacjonarnych musz uzyska
              wynik co najmniej 300 pkt.
            </div>
          </div>
          <div className="uczelnia-segment-img akademia-gorniczo-hutnicza-img">
            <img
              src={
                process.env.PUBLIC_URL + "/images/Uczelnie/AkademiaGorniczoHutnicza.png"
              }
              alt="Akademia Grniczo Hutnicza"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment politechnika-warszawska"
          onClick={() => {
            window.open("https://www.pw.edu.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text politechnika-warszawska-text">
            <div className="uczelnia-segment-start politechnika-warszawska-start">
              <span>Politechnika Warszawska</span>
            </div>
            <div className="uczelnia-segment-content politechnika-warszawska-content">
              Politechnika Warszawska to ceniona publiczna uczelnia techniczna w
              Polsce. Oferuje doskona edukacj w inynierii, naukach cisych
              i spoecznych. Studenci chwal profesjonalizm kadry i bogate
              zaplecze badawcze. Jednak dostanie si jest wymagajce, z wysok
              konkurencj i egzaminami wiedzy. Reputacja uczelni przyciga, ale
              proces rekrutacyjny stanowi wyzwanie. Podczas rekrutacji pod uwag
              jest brany wynik z matury z matematyki, jzyka i obcego i innego
              przedmiotu wybranego w zalenoci od kierunku. Wyniki z matury
              ustnej nie s brane pod uwag.
            </div>
          </div>
          <div className="uczelnia-segment-img politechnika-warszawska-img">
            <img
              src={
                process.env.PUBLIC_URL + "/images/Uczelnie/PolitechnikaWarszawska.png"
              }
              alt="Politechnika Warszawska"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment uniwersytet-warszawski"
          onClick={() => {
            window.open("https://www.uw.edu.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text uniwersytet-warszawski-text">
            <div className="uczelnia-segment-start uniwersytet-warszawski-start">
              <span>Uniwersytet Warszawski</span>
            </div>
            <div className="uczelnia-segment-content uniwersytet-warszawski-content">
              Uniwersytet Warszawski (UW) to renomowana uczelnia w Polsce,
              zajmujca czoowe miejsce w rankingach edukacyjnych. Znany z
              konkurencyjnej rekrutacji, uniwersytet stawia wysokie wymagania, w
              tym prg punktowy wynoszcy 320 PKT. Wydzia Matematyki,
              Informatyki i Mechaniki UW cieszy si szczeglnie siln reputacj
              w dziedzinie matematyki. UW oferuje wysok jako ksztacenia,
              dostp do bogatych zasobw naukowych oraz moliwo wsppracy z
              partnerami midzynarodowymi, co czyni go atrakcyjnym miejscem
              nauki i rozwoju zawodowego.
            </div>
          </div>
          <div className="uczelnia-segment-img uniwersytet-warszawski-img">
            <img
              src={process.env.PUBLIC_URL + "/images/Uczelnie/UniwersytetWarszawski.png"}
              alt="Uniwersytet Warszawski"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment uniwersytet-jagiellonski"
          onClick={() => {
            window.open("https://www.uj.edu.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text uniwersytet-jagiellonski-text">
            <div className="uczelnia-segment-start uniwersytet-jagiellonski-start">
              <span>Uniwersytet Jagielloski w Krakowie</span>
            </div>
            <div className="uczelnia-segment-content uniwersytet-jagiellonski-content">
              Uniwersytet Jagielloski (UJ) to najstarsza uczelnia w Polsce
              cieszca si wieloletni tradycj i prestiem. UJ oferuje wysokiej
              jakoci ksztacenie, dostp do rozbudowanych zasobw naukowych i
              wspprac z midzynarodowymi instytucjami. Rekrutacja obejmuje
              rnorodne kierunki studiw w tym Matematyke czy Informatyke.
              Opinie studentw podkrelaj wysoki poziom nauczania, a minimalny
              prg 310 punktw tworzy rygorystyczne warunki przyjcia.
              Uniwersytet Jagielloski niezmiennie zajmuje czoowe miejsca w
              zestawieniach najlepszych uczelni w Polsce
            </div>
          </div>
          <div className="uczelnia-segment-img uniwersytet-jagiellonski-img">
            <img
              src={
                process.env.PUBLIC_URL + "/images/Uczelnie/UniwersytetJagiellonski.png"
              }
              alt="Politechnika Warszawska"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment uniwersytet-wroclawski"
          onClick={() => {
            window.open("https://www.uwr.edu.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text uniwersytet-wroclawski-text">
            <div className="uczelnia-segment-start uniwersytet-wroclawski-start">
              <span>Uniwersytet Wrocawski</span>
            </div>
            <div className="uczelnia-segment-content uniwersytet-wroclawski-content">
              Uniwersytet Wrocawski to ceniona instytucja edukacyjna w Polsce,
              z bogat histori i rnorodn ofert akademick. Prg punktowy -
              320 PKT stanowi wyzwanie, jednak gwarantuje wysok jako
              nauczania oraz zaawansowane moliwoci rozwoju intelektualnego.
              Uczelnia wyrnia si zaangaowaniem w rozwj kompetencji
              krytycznego mylenia, umacniajc pozycj jako instytucja, gdzie
              studenci rozwijaj nie tylko wiedz, ale take umiejtnoci
              analityczne. To miejsce, gdzie pasjonaci matematyki znajd
              inspirujce wyzwania i dogodne warunki do rozwoju. To wszystko
              sprawia, e Uniwersytet Wrocawski jest postrzegany jako jeden z
              liderw w dziedzinie matematyki w Polsce.
            </div>
          </div>
          <div className="uczelnia-segment-img uniwersytet-wroclawski-img">
            <img
              src={process.env.PUBLIC_URL + "/images/Uczelnie/UniwersytetWroclawski.png"}
              alt="Politechnika Warszawska"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment uniwersytet-lodzki"
          onClick={() => {
            window.open("https://www.uni.lodz.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text uniwersytet-lodzki-text">
            <div className="uczelnia-segment-start uniwersytet-lodzki-start">
              <span>Uniwersytet dzki</span>
            </div>
            <div className="uczelnia-segment-content uniwersytet-lodzki-content">
              Uniwersytet dzki to miejsce dla pasjonatw matematyki, oferujce
              konkurencyjne programy studiw. W rekrutacji na rok 2023,
              minimalny prg punktowy na wybrane kierunki matematyczne wynosi
              280 PKT. Wydzia Nauk Matematycznych wyrnia si zaawansowanymi
              praktykami badawczymi, kadc nacisk na rozwijanie umiejtnoci
              analitycznych i matematycznych, a opinie studentw podkrelaj nie
              tylko wysoki poziom nauczania, ale take przyjazn atmosfer
              akademick
            </div>
          </div>
          <div className="uczelnia-segment-img uniwersytet-lodzki-img">
            <img
              src={process.env.PUBLIC_URL + "/images/Uczelnie/UniwersytetLodzki.png"}
              alt="Uniwersytet dzki"
            />
          </div>
        </div>
        <div
          className="uczelnia-segment politechnika-wroclawska"
          onClick={() => {
            window.open("https://pwr.edu.pl/", "_blank");
          }}
        >
          <div className="uczelnia-segment-text politechnika-wroclawska-text">
            <div className="uczelnia-segment-start politechnika-wroclawska-start">
              <span>Politechnika Wrocawska</span>
            </div>
            <div className="uczelnia-segment-content politechnika-wroclawska-content">
              Politechnika Wrocawska, a zwaszcza Wydzia Podstawowych
              Problemw Techniki, to cenione miejsce dla mionikw matematyki,
              oferujce doskonay poziom ksztacenia. Popularne kierunki, takie
              jak Informatyka Stosowana czy Matematyka, przycigaj studentw z
              caej Polski, a take zagranicy, tworzc wielokulturowe rodowisko
              akademickie. Opinie studentw podkrelaj profesjonalizm kadry
              naukowej, dostp do nowoczesnej infrastruktury badawczej i
              atrakcyjne inicjatywy, takie jak konferencje i seminaria.
              Politechnika Wrocawska wyrnia si zaawansowanymi badaniami
              naukowymi, mocnym powizaniem z praktyk branow oraz nowoczesn
              infrastruktur, co czyni j liderem w dziedzinie matematyki i
              technologii w Polsce.
            </div>
          </div>
          <div className="uczelnia-segment-img politechnika-wroclawska-img">
            <img
              src={
                process.env.PUBLIC_URL + "/images/Uczelnie/PolitechnikaWroclawska.png"
              }
              alt="Politechnika Wrocawska"
            />
          </div>
        </div>
      </div>
    </div>
  );
}
export default Studia;
