import React from "react";
import { Link } from "react-router-dom";
import ClipboardJS from "clipboard";
import "../component-styles/FAQ.scss";

function FAQ() {
  const clipboard = new ClipboardJS(".copy");

  clipboard.on("success", (e) => {
    e.clearSelection();
  });

  return (
    <div className="faq">
      <header className="welcome-faq">
        <span className="line">
          Najczciej zadawane <span className="purple-text">pytania</span>
        </span>
      </header>
      <div className="questions">
        <div className="question">
          <strong></strong>
          <br />
          <h2>Czy korzystanie z matzone.pl jest darmowe?</h2>
          Tak, matzone.pl oferuje wszystkie materiay edukacyjne bezpatnie.
          Naszym celem jest dostarczenie uczniom dostpu do wysokiej jakoci
          zasobw matematycznych zarwno ju dostpnych w internecie jak rwnie
          tworzonych przez nas.
          <h2>Czy matzone.pl jest przeznaczone dla maturzystw?</h2>
          Tak, matzone.pl to platforma dla uczniw szk rednich w tym
          maturzystw. Na naszej stronie w zakadce{" "}
          <Link to="/arkusze">ARKUSZE</Link> znajduj si wszystkie arkusze
          maturalne CKE jak rwnie popularnych wydawnictw wraz z odpowiedziami
          przygotowywanymi przez nas. Ponadto w{" "}
          <Link to="/materialy">MATERIAACH</Link> umieszczamy konkretne
          zadania, ktre pojawiy si na maturze podzielone dziaami tak aby
          maturzyci powtarzajc sobie teorie, mogli wykorzysta j w praktyce.
          <h2>Jak moge wesprze matzone.pl?</h2>
          Jestemy otwarci na wszelk wspprac i wdziczni za kad pomoc.
          Najbardziej zaley nam na rozgosie, dlatego udostpnienie naszej
          platformy jest dla nas szczeglnie pomocne. Zachcamy do dzielenia si
          linkami do naszych materiaw na innych platformach edukacyjnych.
          Wicej informacji znajduje si w zakadce{" "}
          <Link to="/wsparcie">WSPARCIE</Link>.
          <h2>
            Jak mog skontaktowa si z zespoem matzone.pl w razie pyta lub
            sugestii?
          </h2>
          Skontaktuj si z nami poprzez adres e-mailowy:
          <span
            className="copy "
            data-clipboard-text="matzone.kontakt@gmail.com"
          >
            {" "}
            matzone.kontakt@gmail.com
          </span>
          . Jestemy otwarci na wszelkie pytania, uwagi i sugestie od
          uytkownikw.
        </div>
      </div>
    </div>
  );
}

export default FAQ;
